              </v-flex>
              <v-flex xs6>
                <img :src="faceImgSrc" class="img-responsive" width="500px" @click="showDialog(false)" style="cursor: pointer;"/>
              </v-flex>
            </v-layout>
          </v-container>
        </v-flex>
      </v-layout>

      <v-layout row fill-height>
        <v-flex xs12>
