  methods: {
    prevStep (n) {
      if (n === 1) {
        this.setCurrentStep(this.steps)

      } else {
        this.setCurrentStep(n - 1)
      }
    },
    nextStep (n) {
      if (n === this.steps) {
        this.setCurrentStep(1)
      } else {
        this.setCurrentStep(n + 1)
      }
    },
    verify() {
      // console.log('guest', this.guest)
      this.$store.dispatch('checkIn/saveGuest', this.guest)
      this.$store.dispatch('checkIn/putImage', true)
      this.$store.dispatch('checkIn/putImage', false)
    },
    showDialog(isId) {
      EventBus.$emit('dialog', { showDialog: true, isId });
      this.isId = isId;
    },
    onCancelClose() {
      console.log('cancelClose')
    },
