    async onCaptureClose(val) {
      console.log('onCaptureClose1')

      if (val.base64Img) {
        if (val.isId) {
          this.guest.idBase64Img = val.base64Img
          this.guest.idImgSaved = true
          await this.setIdImgSrc()
        } else {
          this.guest.faceBase64Img = val.base64Img
          this.guest.faceImgSaved = true
          await this.setFaceImgSrc()
        }
      }

      console.log('onCaptureClose2')

    },
