<template>
  <v-layout row justify-center>
    <v-dialog v-model="showDialog" fullscreen hide-overlay transition="dialog-bottom-transition">
      <!-- <v-btn slot="activator" color="primary" dark>Open Dialog</v-btn> -->
      <v-card>
        <v-toolbar>
          <v-btn icon @click.native="cancelCloseDialog()">
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>Take Picture</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn icon @click.native="saveCloseDialog()">
              <v-icon>save</v-icon>
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <web-camera @captured="onCapture" v-model="face"></web-camera>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import WebCamera from '@/components/Camera.vue'
import { EventBus } from '@/eventBus'
// import AmplifyStore from '../store'

export default {
  name: 'CameraDialog',
  data () {
    return {
      isId: false,
      showDialog: false,
      base64Img: null,
      face: require('../assets/img/camera.svg')
    }
  },
  created () {
    let vm = this
    EventBus.$on('dialog', function (value) {
      vm.showDialog = value.showDialog
      vm.isId = value.isId
    })
  },
  methods: {
    cancelCloseDialog() {
      this.face = require('../assets/img/camera.svg')

      // transfer the image and notify the parent component of cancelClose
      this.$emit('cancelClose')

      // close the dialog
      EventBus.$emit('dialog', { showDialog: false, isId: this.isId });
    },
    onCapture(img) {
      // this.base64Img = img.split(',')[1]
      this.base64Img = img
    },
    saveCloseDialog() {
      this.face = require('../assets/img/camera.svg')
      
      // transfer the image and notify the parent component of saveClose
      this.$emit('captureClose', { base64Img: this.base64Img, isId: this.isId })
      
      // close the dialog
      EventBus.$emit('dialog', { showDialog: false, isId: this.isId })
    }

  },
  components: {
    WebCamera
  }
}
</script>