
<template>
  <v-container fluid>
    <v-layout row>
      <v-flex xs6>
        <v-layout row fill-height>
          <v-flex xs12>
            <v-select
              v-model="camera"
              item-text="label"
              item-value="deviceId"
              :items="devices"
              standard
              label="Select Camera"
            ></v-select>
          </v-flex>  
          <v-flex>
            <v-btn @click="onStart">
              Start Camera
            </v-btn>
          </v-flex>  
          <v-flex>
            <v-btn @click="onStop">
              Stop Camera
            </v-btn>
          </v-flex>  
        </v-layout> 
      </v-flex>
      <v-flex xs6>
        <span class="headline"><v-btn class="headline" flat @click="onCapture">{{ $t("camera.capture") }}</v-btn></span>
      </v-flex>
    </v-layout>
    
    <v-divider/>

    <v-layout row fill-height>
      <v-flex xs6>
        <v-card tile>
          <web-cam ref="webcam"
                :deviceId="deviceId"
                @started="onStarted" 
                @stopped="onStopped" 
                @error="onError"
                @cameras="onCameras"
                @camera-change="onCameraChange" height="500px"/>
        </v-card>
      </v-flex>
      <v-flex xs6>
        <v-card tile>
          <figure class="figure">
            <img :src="img" class="img-responsive" height="500px" @click="onCapture()" style="cursor: pointer;"/>
          </figure>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import {WebCam} from 'vue-web-cam'
import {find, head} from 'lodash';
export default {
  name: 'Camera',
  components: {
    WebCam
  },
  props: ['value'],
  data() {
    return {
      // img: this.face,
      camera: null,
      deviceId: null,
      devices: []
    };
  },
  computed: {
    device: function() {
      return find(this.devices, n => n.deviceId == this.deviceId);
    },
    img: {
      get: function () {
        return this.value
      },
      set(val){
        this.$emit('input', val)
      }
    },
  },
  watch: {
    camera: function(id) {
      this.deviceId = id;
    },
    devices: function() {
      // Once we have a list select the first one
      let first = head(this.devices);
      if(first) {
        this.camera = first.deviceId;
        this.deviceId = first.deviceId;
      }
    }
  },
  methods: {
    onCapture() {
      let captured = this.$refs.webcam.capture();
      this.img = captured
      // console.log('camera.vue onCapture', captured)
      this.$emit('captured', captured)
    },
    onStarted(stream) {
      // console.log('On Started Event', stream);
    },
    onStopped(stream) {
      // console.log('On Stopped Event', stream);
    },
    onStop() {
      this.$refs.webcam.stop();
    },
    onStart() {
      this.$refs.webcam.start();
    },
    onError(error, stream) {
      // console.log('On Error Event', error, stream);
    },
    onCameras(cameras) {
      this.devices = cameras;
      // console.log('On Cameras Event', cameras);
    },
    onCameraChange(deviceId) {
      this.deviceId = deviceId;
      this.camera = deviceId
      // console.log('On Camera Change Event', deviceId);
    }
  }
};
</script>
