const state = {
  user: null,
  userIdentityId: null
}

const mutations = {
  setUser(state, user) {
    state.user = user
  },
  setUserIdentityId(state, userIdentityId) {
    state.userIdentityId = userIdentityId
  }
}

const actions = {
  setUser ({ commit }, user) {
    commit('setUser', user)
  },
  setUserIdentityId ({ commit }, userIdentityId) {
    commit('setUserIdentityId', userIdentityId)
  }
}

const getters = {}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}