import Amplify, { API, graphqlOperation } from "aws-amplify";
import * as gql from './appsync-gql'

export default {
  async retrieveGuests (reservationCode) {
    const rtn = await API.graphql(
    		graphqlOperation(
    			gql.getReservationGuest, { reservationCode: reservationCode }
      	)
    	)
      .catch(e => console.log('retrieveGuests', e))

    return rtn.data.getReservationGuest
  },
  async retrieveReservation (reservationCode) {
    const rtn = await API.graphql(
        graphqlOperation(
          gql.getReservation, { reservationCode: reservationCode }
        )
      )
      .catch(e => console.log('retrieveReservation', e))

    return rtn.data.getReservation
  },
  async createReservationGuest (params) {
    const rtn = await API.graphql(graphqlOperation(gql.createReservationGuest, params))
      .catch(e => console.log('createReservationGuest', e))

    return rtn.data.createReservationGuest
  },
  async updateReservationGuest (params) {
    const rtn = await API.graphql(graphqlOperation(gql.updateReservationGuest, params))
      .catch(e => console.log('updateReservationGuest',e))

    return rtn.data.updateReservationGuest
  }
}